.view-selector {
    position: absolute;
    left: 5px;
    top: 12px;
    z-index: 999;
}

.video-icon {
    color: white;
    font-size: 24px;
    padding: 1rem;
    border: 1px solid white;
    cursor: pointer;
}

.select-camera-type:nth-child(1) {
    opacity: 1;
    transition: .7s;
}

.view-selector .view-selector-item {
    padding: 0;
    width: 40px;
    height: 40px;
    border: 1px solid #333;
    color: white;
    cursor: pointer;
}

.view-selector .view-selector-item:hover {
    background: #888;
}

.view-selector .view-selector-item.active {
    filter: invert(100%);
    background: #333;
}